


















export default {
	data() {
		return {
			show: true,
			count: '',
			timer:null,
			mobile: '',
			verifyCode: ''
		};
	},
	onLoad() {
		uni.setNavigationBarTitle({
			title: '注册'
		});
	},
	methods: {
		getVerifyCode: function() {
			var _this = this;
			const TIME_COUNT = 60;
			uni.request({
				url: this.apiServer + '/user/verify',
				method: 'POST',
				header: { 'content-type': 'application/x-www-form-urlencoded' },
				data: {
					mobile: _this.mobile
				},
				success: res => {
					if (res.data.code === 0) {
						uni.showToast({
							title: '验证码已发送'
						});
						_this.disabled = true;
						console.log(_this.disabled);
						if (!this.timer) {
						  this.count = TIME_COUNT;
						  this.show = false;
						  this.timer = setInterval(() => {
						    if (this.count > 0 && this.count <= TIME_COUNT) {
						      this.count--;
						    } else {
						      this.show = true;
						      clearInterval(this.timer);
						      this.timer = null;
						    }
						  }, 1000)
						}
					} else {
						uni.showModal({
							title: '提示',
							content: res.data.msg
						});
					}
					
				}
			});
		},
		checkCode: function() {
			var _this = this;
			console.log(_this.verifyCode);
			uni.request({
				url: this.apiServer + '/user/check',
				method: 'POST',
				header: { 'content-type': 'application/x-www-form-urlencoded' },
				data: {
					mobile: _this.mobile,
					verifyCode: _this.verifyCode
				},
				success: res => {
					console.log(res.data);
					if (res.data.code === 0) {
						uni.navigateTo({
							url: '../signin/password?mobile=' + _this.mobile
						});
					} else {
						uni.showModal({
							title: '提示',
							content: res.data.msg
						});
					}
				}
			});
		}
	}
};
